<template>
  <div class="app-container">
    <el-form v-show="showSearch" ref="queryForm" :model="queryParams" :inline="true">
      <el-form-item label="" prop="menuName">
        <el-input
          v-model="queryParams.menuName"
          placeholder=""
          clearable
          size="small"
          @keyup.enter.native="handleQuery"
        />
      </el-form-item>
      <el-form-item label="" prop="status">
        <el-select v-model="queryParams.status" placeholder="" clearable size="small">
          <el-option :key="0" label="" :value="0" />
          <el-option :key="1" label="" :value="1" />
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" icon="el-icon-search" size="mini" @click="handleQuery"></el-button>
      </el-form-item>
    </el-form>

    <el-row :gutter="10" class="mb8">
      <el-col :span="1.5">
        <el-button
          type="primary"
          plain
          icon="el-icon-plus"
          size="mini"
          @click="handleAdd"
        ></el-button>
      </el-col>
    </el-row>

    <el-table
      v-if="refreshTable"
      v-loading="loading"
      :data="menuList"
      row-key="id"
      :default-expand-all="isExpandAll"
      :tree-props="{children: 'children', hasChildren: 'hasChildren'}"
    >
      <el-table-column prop="menuName" label="" :show-overflow-tooltip="true" width="160" />
      <el-table-column prop="icon" label="" align="center" width="100">
        <template slot-scope="scope">
          <svg-icon :icon-class="scope.row.icon" />
        </template>
      </el-table-column>
      <el-table-column prop="orderNum" label="" width="60" />
      <el-table-column prop="perms" label="" :show-overflow-tooltip="true" />
      <el-table-column prop="component" label="" :show-overflow-tooltip="true" />
      <el-table-column prop="status" label="" width="80">
        <template slot-scope="scope">
          <el-tag v-if="scope.row.status==0" type="success"></el-tag>
          <el-tag v-if="scope.row.status==1" type="danger"></el-tag>
        </template>
      </el-table-column>
      <el-table-column label="" align="center" prop="createTime">
        <template slot-scope="scope">
          <span>{{ parseTime(scope.row.createTime) }}</span>
        </template>
      </el-table-column>
      <el-table-column label="" align="center" class-name="small-padding fixed-width">
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="text"
            icon="el-icon-edit"
            @click="handleUpdate(scope.row)"
          ></el-button>
          <el-button
            size="mini"
            type="text"
            icon="el-icon-plus"
            @click="handleAdd(scope.row)"
          ></el-button>
          <el-button
            size="mini"
            type="text"
            icon="el-icon-delete"
            @click="handleDelete(scope.row)"
          ></el-button>
        </template>
      </el-table-column>
    </el-table>

    <!--  -->
    <el-dialog :title="title" :visible.sync="open" width="680px" append-to-body>
      <el-form ref="form" :model="form" :rules="rules" label-width="100px">
        <el-row>
          <el-col :span="24">
            <el-form-item label="">
              <treeselect
                v-model="form.parentId"
                :options="menuOptions"
                :normalizer="normalizer"
                :show-count="true"
                placeholder=""
              />
            </el-form-item>
          </el-col>
          <el-col :span="24">
            <el-form-item label="" prop="menuType">
              <el-radio-group v-model="form.menuType">
                <el-radio label="M"></el-radio>
                <el-radio label="C"></el-radio>
                <el-radio label="F"></el-radio>
              </el-radio-group>
            </el-form-item>
          </el-col>
          <el-col :span="24">
            <el-form-item v-if="form.menuType != 'F'" label="">
              <!-- <IconSelect ref="iconSelect" @selected="selected" /> -->
              <el-select
                v-model="form.icon"
                placeholder=""
                clearable
                size="small"
                style="width: 240px"
              >
                <el-option v-for="(item, index) in icons" :key="index" :value="item">
                  <svg-icon :icon-class="item" />
                  <span>{{ item }}</span>
                </el-option>

              </el-select>

            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="menuName">
              <el-input v-model="form.menuName" placeholder="" />
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="orderNum">
              <el-input-number v-model="form.orderNum" controls-position="right" :min="0" />
            </el-form-item>
          </el-col>
          <el-col :span="24">
            <el-form-item v-if="form.menuType != 'F'" prop="path">
              <span slot="label">
                <el-tooltip content="`user`" placement="top">
                  <i class="el-icon-question" />
                </el-tooltip>
                
              </span>
              <el-input v-model="form.path" placeholder="" />
            </el-form-item>
          </el-col>
          <el-col v-if="form.menuType == 'C'" :span="12">
            <el-form-item prop="component">
              <span slot="label">
                <el-tooltip content="`system/user/index``views`" placement="top">
                  <i class="el-icon-question" />
                </el-tooltip>
                
              </span>
              <el-input v-model="form.component" placeholder="" />
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item v-if="form.menuType != 'M'">
              <el-input v-model="form.perms" placeholder="" maxlength="100" />
              <span slot="label">
                <el-tooltip content="@PreAuthorize(`@ss.hasPermi('system:user:list')`)" placement="top">
                  <i class="el-icon-question" />
                </el-tooltip>
                
              </span>
            </el-form-item>
          </el-col>

          <el-col :span="12">
            <el-form-item v-if="form.menuType != 'F'">
              <span slot="label">
                <el-tooltip content="" placement="top">
                  <i class="el-icon-question" />
                </el-tooltip>
                
              </span>
              <el-radio-group v-model="form.visible">
                <el-radio :key="'0'" :label="'0'"></el-radio>
                <el-radio :key="'1'" :label="'1'"></el-radio>
              </el-radio-group>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item v-if="form.menuType != 'F'">
              <span slot="label">
                <el-tooltip content="" placement="top">
                  <i class="el-icon-question" />
                </el-tooltip>
                
              </span>
              <el-radio-group v-model="form.status">
                <el-radio :key="'0'" :label="'0'"></el-radio>
                <el-radio :key="'1'" :label="'1'"></el-radio>
              </el-radio-group>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="submitForm"> </el-button>
        <el-button @click="cancel"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { listMenu, getMenu, delMenu, addMenu, updateMenu } from '@/api/menu'
import Treeselect from '@riophae/vue-treeselect'
import '@riophae/vue-treeselect/dist/vue-treeselect.css'

export default {
  name: 'Menu',
  components: { Treeselect },
  data() {
    return {
      // 
      loading: true,
      // 
      showSearch: true,
      // 
      menuList: [],
      // 
      menuOptions: [],
      // 
      title: '',
      // 
      open: false,
      // 
      isExpandAll: false,
      // 
      refreshTable: true,
      // 
      queryParams: {
        menuName: undefined,
        visible: undefined
      },
      icons: [],
      // 
      form: {},
      // 
      rules: {
        menuName: [
          { required: true, message: '', trigger: 'blur' }
        ],
        orderNum: [
          { required: true, message: '', trigger: 'blur' }
        ],
        path: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
    }
  },
  created() {
    this.getList()
    this.icons = this.getIcons()
  },
  methods: {
    getIcons() {
      const req = require.context('@/assets/icons/svg', false, /\.svg$/)
      const requireAll = requireContext => requireContext.keys()
      const re = /\.\/(.*)\.svg/

      const arr = requireAll(req).map(i => {
        return i.match(re)[1]
      })
      return arr
    },
    // 
    selected(name) {
      this.form.icon = name
    },
    /**  */
    getList() {
      this.loading = true
      listMenu(this.queryParams).then(response => {
        this.menuList = this.handleTree(response, 'id')
        this.loading = false
      })
    },
    /**  */
    normalizer(node) {
      if (node.children && !node.children.length) {
        delete node.children
      }
      return {
        id: node.id,
        label: node.menuName,
        children: node.children
      }
    },
    /**  */
    getTreeselect() {
      listMenu().then(response => {
        this.menuOptions = []
        const menu = { id: 0, menuName: '', children: [] }
        menu.children = this.handleTree(response, 'id')
        this.menuOptions.push(menu)
      })
    },
    // 
    cancel() {
      this.open = false
      this.reset()
    },
    // 
    reset() {
      this.form = {
        id: undefined,
        parentId: 0,
        menuName: undefined,
        icon: undefined,
        menuType: 'M',
        orderNum: undefined,
        isCache: '0',
        visible: '0',
        status: '0'
      }
      this.resetForm('form')
    },
    /**  */
    handleQuery() {
      this.getList()
    },
    /**  */
    handleAdd(row) {
      this.reset()
      this.getTreeselect()
      if (row != null && row.id) {
        this.form.parentId = row.id
      } else {
        this.form.parentId = 0
      }
      this.open = true
      this.title = ''
    },

    /**  */
    handleUpdate(row) {
      this.reset()
      this.getTreeselect()
      getMenu(row.id).then(response => {
        this.form = response
        this.open = true
        this.title = ''
      })
    },
    /**  */
    submitForm: function() {
      this.$refs['form'].validate(valid => {
        if (valid) {
          if (this.form.id !== undefined) {
            updateMenu(this.form).then(response => {
              this.$modal.msgSuccess('')
              this.open = false
              this.getList()
            })
          } else {
            addMenu(this.form).then(response => {
              this.$modal.msgSuccess('')
              this.open = false
              this.getList()
            })
          }
        }
      })
    },
    /**  */
    handleDelete(row) {
      this.$modal.confirm('"' + row.menuName + '"').then(function() {
        return delMenu(row.id)
      }).then(() => {
        this.getList()
        this.$modal.msgSuccess('')
      }).catch(() => {})
    }
  }
}
</script>
